import { Component, OnInit } from '@angular/core';
import { AuthService } from "src/app/services/auth.service"
import { AlertController } from '@ionic/angular';
import { Router } from '@angular/router';

interface User {
  email: string
  password: string
  repeatPassword: string
}
@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.page.html',
  styleUrls: ['./sign-up.page.scss'],
})


export class SignUpPage implements OnInit {
  user: User = {
    email: "",
    password: "",
    repeatPassword: ""
  }
  constructor(
    private auth: AuthService,
    public alertController: AlertController,
    private router: Router,
  ) { }
  async presentAlert(msg: string, header?: string, callback?: any) {
    const alert = await this.alertController.create({
      cssClass: 'alert-box',
      header: header,
      message: msg,
      buttons: [{
        text: 'OK', handler: () => {
          if (callback != undefined) callback()

        }
      }],

    });

    await alert.present();
  }
  ngOnInit() {
  }
  onSubmit() {
    if (this.user.password === this.user.repeatPassword) {
      this.auth.SignUp(this.user.email, this.user.password)
        .then(data => {
          console.log(data)
          if (data.message) this.presentAlert(data.message)
          else if (data) this.presentAlert("Please validate your email address. Kindly check your inbox.", "You successfully registered", () => this.router.navigateByUrl("/sign-in"))
        })
    }
    else this.presentAlert("Those passwords didn't match.")
  }
}
